body {
  font: 14px 'Century Gothic', Futura, sans-serif;
  margin: 0 auto;
  box-sizing: border-box;
  background-color: beige;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  margin-bottom: 20px;
  font-size: 20px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 50px;
  font-weight: bold;
  line-height: 100px;
  height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 100px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
}

.game-info {
  margin-left: 20px;
}

@media screen and (max-width: 500px) {
  .square {
    font-size: 20px;
    line-height: 50px;
    height: 50px;
    width: 50px;
  }
}
